@import url(https://cdn.syncfustion.com/ej2/material.css);
.Main__container {
  background-color: rgba(0, 0, 0, 0.2);
  padding: 15px;
}

.Main {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Main div {
  width: 150px;
  height: 150px;
}

.Main__day {
  border: 2px solid white;
  margin-right: 1px;
  display: flex;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.Main a {
  color: black;
  text-decoration: none;
}

.Main__day div {
  font-size: 36px;
  font-weight: 800;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

:is(.Main a:first-child, .Main a:last-child) .Main__day div {
  color: #e66767;
}

:is(.Main a:nth-child(2), .Main a:nth-child(7)) .Main__day div {
  color: white;
}

:is(.Main a:nth-child(3), .Main a:nth-child(4), .Main a:nth-child(5), .Main
    a:nth-child(6))
  .Main__day
  div {
  color: #546de5;
}

.DayContainer__box img,
.DayContainer__box video {
  /* position: relative; */
  /* top: -50px; */
  width: 225px;
  height: 300px;
  margin: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.DayContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
}

.DayContainer img:hover {
  -webkit-animation: hoverImg 0.1s linear forwards;
          animation: hoverImg 0.1s linear forwards;
}

.clickedBox {
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.clickedBox__pic {
  width: 80%;
  height: 80%;
  border-radius: 15px;
  z-index: 2;
}

@-webkit-keyframes hoverImg {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes hoverImg {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

.noshow {
  display: none;
}
