.goBack,
.goNext {
  cursor: pointer;
  color: black;
  opacity: 0.7;
}

.goBack :hover,
.goNext :hover {
  color: #347edd;
}

.DayContainer__btn {
  display: flex;
  justify-content: space-between;
}

.backgroundImg {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-size: cover;
}

p {
  position: absolute;
  font-size: 18px;
  bottom: 5px;
}

